import { AuthenticationService } from './../authentication.service';
import { environment } from './../../environments/environment';
import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';


@Injectable()
export class CursosService {

  constructor(private http: Http, private authenticationService: AuthenticationService) { }

  myList = [];

  private api = `${environment.url}cursos/`;



  findAll() {
    this.http.get(this.api)
      .subscribe(response => {
        this.myList = response.json();
      })

  }

  createItem(newItem) {

    var options = new RequestOptions({
      'headers': new Headers({
        'Authorization':'Bearer '+ localStorage.getItem('token')
      })
    })

    this.http.post(this.api, newItem,options)
      .subscribe(response => {
        this.myList.push(response.json());
      })

  }

  removeItem(item) {

      var options = new RequestOptions({
      'headers': new Headers({
        'Authorization':'Bearer '+ localStorage.getItem('token')
      })
    })

    this.http.delete(`${this.api}${item._id}`, options)
      .subscribe(response => {
        if (response.json().message === 'success') {
          let index = this.myList.indexOf(item);
          this.myList.splice(index, 1);
        }
      })
  }

}



