import { environment } from './../environments/environment';
import { Http } from '@angular/http';
import { Injectable } from '@angular/core';


@Injectable()
export class AuthenticationService {

  isLogged = false;
  token;

  constructor(private http: Http) {
  }
    login(user, callback){
      this.http.post(`${environment.url}cursos/login`, user)
        .subscribe(response => {
          var resp = response.json();
          if (resp.message === 'ok') {
             this.isLogged = true;
            this.token = localStorage.setItem('token', resp.token);
         
    
          }else{
            this.isLogged = false;
          }
        
          callback(this.isLogged);
        })
    }

}



